e.png') no-repeat bottom left;\n            background-size: 370px;\n          }\n        }\n        .acturally {\n          font-size: 36px;\n          width: 40px;\n        }\n      }\n      .tip {\n        font-size: 32px;\n        color: #666;\n        margin-bottom: 45px;\n        text-align: center;\n      }\n      .grade {\n        justify-content: center;\n      }\n      .edit-input {\n        margin-top: 40px;\n        border-bottom: 1px solid #e5e5e5;\n        border-top: 1px solid #e5e5e5;\n        padding: 30px 30px 16px;\n        color: #666;\n        height: 350px;\n        box-sizing: border-box;\n        textarea {\n          width: 100%;\n          height: calc(100% - 56px);\n          display: block;\n          font-size: 32px;\n          line-height: 1.5;\n          border: 0;\n        }\n        .text-right {\n          line-height: 40px\n        }\n      }\n      .edit-publish {\n        padding: 42px 0 38px;\n        font-size: 36px;\n        line-height: 50px;\n        box-sizing: border-box;\n        text-align: center;\n        border-top: 1px solid #f5f5f5;\n        &.active {\n          color: #F4A82B;\n        }\n      }\n    }\n  }\n}\n#movieImage {\n  height: 100vh;\n  box-sizing: border-box;\n  overflow: auto;\n \n}\n",null],"sourceRoot":""}]);

